body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
	height: 100%;
	width: 100%;
	font-family: 'Charmonman', cursive;
}

.App {
	text-align: center;
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 40vmin;
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.first_page{
    color: white;
    background-color: rgb(0, 60, 255);
} 
.first_page .Arrow_down_first{
    position: absolute;
    top:90vh;
    align-items: center;
}
.full_page {
	height: 100vh;
	width: 100vw;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	
}


.Third_page {
	background: bisque;
}

.Third_page .flexiy {
	display: flex;
	flex: auto;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
.Third_page h1 {
	font-size: 6em;
	position: absolute;
	top: 210vh;
	text-align: center;
	padding-bottom: 0;
	color: blue;
}

.Third_page section {
	height: 20vh;
	width: 15vw;
	margin: 10vw;
	margin-top: -10vh;
}

.Third_page p {
	font-size: 1.5em;
	padding-top: 3vh;
	padding-left: 2vh;
	padding-right: 2vh;
	color: rgb(133, 69, 194);
}
.Third_page .background_skills {
	background-color: aqua;
	border-radius: 2vh;
}
.Third_page .Arrow_up {
	height: 3vh;
	width: 1.5vw;
	position: absolute;
	top: 295vh;
	align-items: center;
}

@media screen and (min-width: 100px) {
	.Third_page img {
		height: 3vh;
		width: 6vw;
		border-bottom-left-radius: 2vh;
		border-bottom-right-radius: 3vh;
	}
	.Third_page p {
		font-size: 0.5em;
	}
}

@media screen and (min-width: 430px) {
	.Third_page img {
		height: 15vh;
		width: 10vw;
	}
	.Third_page p {
		font-size: 1em;
	}
}
