.about {
	background-color: #6c3483;
}

.about h1 {
	font-size: 6em;
	position: absolute;
	top: 110vh;
	text-align: center;
	padding-bottom: 0;
	color: whitesmoke;
}
.about p {
	font-size: 3em;
	position: absolute;
	top: 140vh;
	text-align: center;
	padding-bottom: 0;
	color: whitesmoke;
	
}
.about .Arrow_down_second {
	position: absolute;
	top: 190vh;
    align-items: center;
}

@media screen and (min-width: 100px) {
	.about h1 {
		font-size: 2em;
	}
	.about p {
		font-size: 1em;
		margin-top: -18vh;
		
	}
}

@media screen and (min-width: 430px) {

	.about h1 {
		font-size: 3em;
	}
	.about p {
		font-size: 2em;
		margin-top: -15vh;
		
	}
}
@media screen and (min-width: 700px) {

	.about h1 {
		font-size: 4em;
	}
	.about p {
		font-size: 2.5em;
		margin-top: -15vh;
		
	}
}
@media screen and (min-width: 1000px) {

	.about h1 {
		font-size: 6em;
	}
	.about p {
		font-size: 3em;
		margin-top: -5vh;
		
	}
}
